@import url(https://fonts.googleapis.com/css2?family=Kodchasan:wght@400;700&family=Roboto&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html{width:100%;height:100%}body{background-image:url(/static/media/background.243421ec.jpg);background-size:cover;background-repeat:no-repeat;background-position:center;-webkit-backdrop-filter:blur(50px);backdrop-filter:blur(50px);width:100%;height:100%;padding:32px;display:flex;justify-content:center;align-items:center;font-family:"Kodchasan",sans-serif;box-sizing:border-box}.header{color:#6E2FBC;font-size:48px;font-weight:bold;text-align:center}p{font-size:18px}@media screen and (max-width: 605px){body{padding:0}}.chatbox{background-color:#FFF7FC;box-shadow:0 4px 20px 0 rgba(0,0,0,0.1);height:80vh;width:605px;min-height:300px;border-radius:12px;padding:16px 48px;display:flex;flex-direction:column;justify-content:flex-end;align-items:flex-end;overflow:hidden;box-sizing:border-box}.chatbox__actions{margin-top:32px;width:100%;min-height:80px;display:flex;justify-content:flex-end;align-items:center}.chatbox__messages{display:flex;flex-direction:column}@media screen and (max-width: 605px){.chatbox{width:100%;height:90vh}}.message{padding:16px 32px;border-radius:30px;background-color:white;color:#6E2FBC;box-shadow:0 4px 16px rgba(0,0,0,0.1);position:relative;margin-bottom:16px;-webkit-animation:fade-in-slide-left 0.4s ease 0s 1;animation:fade-in-slide-left 0.4s ease 0s 1}.message p{font-weight:bold;text-align:left;width:100%}.message:not(.message--loading) .avatar{position:absolute;top:-60px;right:-40px}.message::after{bottom:78px;right:-12px;border:solid transparent;content:'';height:0;width:0;position:absolute;pointer-events:none;border-bottom-color:white;border-width:20px;margin-left:20px;transform:rotate(30deg)}.message--initial{margin-top:48px}.message--loading{padding:16px 48px;width:60px;display:flex;justify-content:center;align-self:flex-end}.message--loading::after{bottom:70%;right:-10%;transform:rotate(35deg)}.message--grouped::after{display:none}.message--sender-bot{align-self:flex-end}.message-sender-bot{align-self:flex-start}@media screen and (max-width: 605px){.message p{font-size:14px}}@-webkit-keyframes fade-in-slide-left{0%{opacity:0;transform:translate(40px, 30px)}100%{opacity:1;transform:translate(0px, 0px)}}@keyframes fade-in-slide-left{0%{opacity:0;transform:translate(40px, 30px)}100%{opacity:1;transform:translate(0px, 0px)}}.action-button{border-radius:30px;outline:none;border:0;background:none;box-shadow:none;background:linear-gradient(180deg, #5B3758 0%, #3A7189 100%);font-family:"Kodchasan",sans-serif;font-size:18px;text-align:center;padding:4px 48px;color:white;font-weight:bold;height:55px;margin-left:16px;cursor:pointer;white-space:nowrap;-webkit-animation:fade-in-slide-up 0.6s ease 0s 1;animation:fade-in-slide-up 0.6s ease 0s 1}.action-button:hover{box-shadow:0 0 10px #3A7189;border:1px solid #3A7189;box-sizing:border-box}@media screen and (max-width: 605px){.action-button{padding:4px 16px;font-size:14px;height:45px}}@-webkit-keyframes fade-in-slide-up{0%{opacity:0;transform:translate(0px, 30px)}100%{opacity:1;transform:translate(0px, 0px)}}@keyframes fade-in-slide-up{0%{opacity:0;transform:translate(0px, 30px)}100%{opacity:1;transform:translate(0px, 0px)}}.loading{display:flex}.loading .loading__dot{position:relative;width:12px;height:12px;margin:5px;border-radius:50%}.loading .loading__dot::before{position:absolute;content:"";width:100%;height:100%;background:inherit;border-radius:inherit;-webkit-animation:wave 1s ease-out infinite;animation:wave 1s ease-out infinite}.loading .loading__dot:nth-child(1){background:#3A7189}.loading .loading__dot:nth-child(1)::before{-webkit-animation-delay:.2s;animation-delay:.2s}.loading .loading__dot:nth-child(2){background:#FFE1F7}.loading .loading__dot:nth-child(2)::before{-webkit-animation-delay:.4s;animation-delay:.4s}.loading .loading__dot:nth-child(3){background:#6E2FBC}.loading .loading__dot:nth-child(3)::before{-webkit-animation-delay:.6s;animation-delay:.6s}@-webkit-keyframes wave{50%,75%{transform:scale(1.5)}80%,100%{opacity:0}}@keyframes wave{50%,75%{transform:scale(1.5)}80%,100%{opacity:0}}.register-form form,.login-form form{width:100%;min-width:200px;min-height:150px;display:flex;flex-direction:column;justify-content:center;align-items:center}.register-form h3,.login-form h3{margin-bottom:32px;width:100%;text-align:center}.register-form label,.login-form label{color:#333;margin-bottom:48px;width:100%}.register-form input,.login-form input{border:none;outline:none;margin-top:8px;border-bottom:1px solid #3A7189;font-family:"Roboto",sans-serif;font-size:18px;width:100%;border-radius:3px;padding:4px}.register-form input:focus,.login-form input:focus{box-shadow:0 0 1px #3A7189}.register-form input::-webkit-input-placeholder, .login-form input::-webkit-input-placeholder{color:#333;opacity:0.2}.register-form input:-ms-input-placeholder, .login-form input:-ms-input-placeholder{color:#333;opacity:0.2}.register-form input::-ms-input-placeholder, .login-form input::-ms-input-placeholder{color:#333;opacity:0.2}.register-form input::placeholder,.login-form input::placeholder{color:#333;opacity:0.2}.register-form .form__placeholder-success,.login-form .form__placeholder-success{display:flex;justify-content:center;align-items:center}.register-form .form__placeholder-success svg,.login-form .form__placeholder-success svg{width:60px}

